@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(22,180,203);
  background: linear-gradient(90deg, rgba(22,180,203,1) 0%, rgba(121,9,86,1) 100%);
}


.margin-lad {
  margin-right: 10px;
  margin-left: 10px;
}

.App {
  text-align: center;

}

.particles {
  position: fixed;
  top:0;
  right:0;
  left:0;
  bottom:0;
  z-index: -1;
}
header {
  height: 70px;
  background: linear-gradient(90deg, rgba(22,180,203,1) 0%, rgba(121,9,86,1) 100%);
  box-shadow: -10px 0 20px rgba(0,0,0,.3);
  box-shadow: inset 0 10px 30px rgba(255,255,255,.1);
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.logoBox {
  display:flex;
  justify-content: flex-start;
  margin-left:20px;
  align-items: center;
  cursor:pointer;
}

.logoBox p {
  font-family:'Roboto';
  color:white;
  margin:0;
  margin-left:15px;
  font-size:22px;
  letter-spacing: 1px;
  font-weight: 600;
  transition: all ease 300ms;
}

.logoBox p:hover {
  color:rgb(36, 226, 255)
}

nav {
  display:flex;
  justify-content: flex-end;
  margin-right:10px;
}

nav p {
  margin:0;
  color: #fff;
  padding:20px;
  cursor: pointer;
  transition: all ease 300ms;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 1px;
}

nav p:hover{
  color:rgb(36, 226, 255);
}

@media screen and (max-width:500px) {
  .logoBox p {
    display:none;
  }
  nav p {
    font-size: 14px
  }
}
.imageLinkForm {
  display:flex;
  justify-content: center;
  align-items:center;
  flex-direction: column;
}

.imageLinkForm p {
  font-size: 26px;
  font-weight: 600;
  color:white;
}

.formBox {
  width:100%;
  max-width: 600px;
  display:flex;
  justify-content: center;
  align-items:center;
}

.formInput {
  width: 70%;
  padding: 10px 15px;
  font-size: 16px;
  border-top-left-radius: 10px;
  border: none;
  outline: none;
  border-bottom-left-radius: 10px;
}

.formInput:focus {
  box-shadow: 0 0 10px #fff;
}

.formButton {
  border: none;
  outline: none;
  width:30%;
  font-size: 16px;
  padding: 10px 15px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  cursor: pointer;
  background-color:rgba(22,180,203,1);
  font-weight: 600;
  color: rgba(121,9,86,1);
  transition: all ease 200ms;
  
}

.formButton:hover {
  transform:  scale(1.1);
  border-radius: 10px;
  color:white;
  box-shadow:inset 0 0 10px #ffffffff;
  transform: translateX(10px);
}
.textBox {
  display:flex;
  justify-content: center;
  
}

.userText {
  font-size: 30px;
  color: white;
  padding: 1px 20px;
  background:linear-gradient(90deg, rgba(22,180,203,1) 0%, rgba(121,9,86,1) 100%);
  display:flex;
  justify-content: center;
  align-items:center;
  border-radius:20px;
  margin: 40px 10px;
  width:100%;
}



.userText span {
  /* linear-gradient(90deg,  0%, 100%); */
  color: yellow;
  font-weight: 600;
}
.imgBoxContainer {
  margin-top: 20px;
  width:100%;
  margin:0 auto;
}

.imgBox {
  display:flex;
  justify-content: center;
  align-items: center;
  max-width: 500px;
  position: relative;
  margin: 0 auto;
}

.img {
 width:100%;
}

.tilt-img {
  margin: 35px auto 60px auto;
}


.bounding-box {
  position: absolute;
  border: 2px solid#16B4CB
  ;
  box-shadow: 0 0 10px #fff;
  cursor:pointer;
}

.bounding-box:hover {
  box-shadow: 0 0 30px #fff;
}

.hidden {
  display:none;
}

.loadingBox {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;

  top: 0;
  left: 0;
  right:0;
  bottom: 0;
}

.textError {
  color: red;
  margin:0;
  background-color: rgba(255, 202, 202, .8);
  border: 2px solid red;
  border-radius: 10px;
  padding:10px 20px;
}

.loggin {
  width:100%;
  display:flex;
  justify-content: center;
  align-items: center;
  height: calc(90vh - 100px)
}

.logginBox {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display:flex;
  max-width:500px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding:30px 50px;
  background: #ffffff11;
  border: 1px solid #fff;
  box-shadow: 3px 3px 5px rgba(0,0,0,.9);
  border-radius:20px;
  color:#fff;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 24px;
}

.logginBox h2 {

}

.logginBox input {
  padding:10px;
  margin: 10px 0;
  border-radius: 5px;
  outline: none;
  border: 0px;
  width: 100%;
  max-width:400px;
  font-size: 18px;
}

.logginBox button {
  margin: 15px;
  padding:10px 20px;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius:5px;
  font-weight: 600;
  color: #fff;
  font-size: 18px;
  cursor: pointer;
  transition: all ease 250ms;
}

.logginBox button:hover {
  background: #000;
}

.logginRegister {
  font-size: 14px;
  text-decoration: underline;
  letter-spacing: 0;
  margin:0;
  cursor: pointer;

}

.logginRegister:hover {
  opacity: .7;
}
